.mml__icon {
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  background-color: #f0624799;
  border: 1px solid #fff3;
  border-radius: 3rem;
  padding: 1.6rem 1.8rem;
  transition: all .3s;
  display: inline-block;
  position: absolute;
}

.mml__icon svg {
  width: 8rem;
  height: auto;
  color: #fff;
}

.mml__icon svg path {
  stroke-width: 1px;
}

.mml__icon--top {
  top: 30px;
  left: 35%;
  transform: rotate(-14deg);
}

.mml__icon--bottom {
  bottom: 30px;
  right: 35%;
  transform: rotate(-8deg);
}

.mml__icon--right {
  top: 80px;
  right: 7%;
  transform: rotate(15deg);
}

.mml__icon--right svg {
  width: 10rem;
}

.mml__icon--left {
  bottom: 80px;
  left: 5%;
  transform: rotate(15deg);
}

.mml__icon--left svg {
  width: 10rem;
}

@media (width >= 670px) {
  .mml:hover .mml__icon {
    background-color: #f06247;
  }

  .mml:hover .mml__icon--top {
    top: 0;
    left: -3%;
  }

  .mml:hover .mml__icon--bottom {
    bottom: -20px;
    right: -5%;
  }

  .mml:hover .mml__icon--right {
    top: 0;
    right: -7%;
  }

  .mml:hover .mml__icon--left {
    bottom: -20px;
    left: -5%;
  }
}

@media (width <= 670px) {
  .mml__icon {
    background-color: #f06247;
    border-radius: 2rem;
  }

  .mml__icon svg {
    width: 7rem;
  }

  .mml__icon--top {
    top: 0;
    left: -7%;
    transform: rotate(-5deg);
  }

  .mml__icon--bottom {
    bottom: -20px;
    right: -5%;
    transform: rotate(-5deg);
  }

  .mml__icon--right {
    top: 0;
    right: -7%;
    transform: rotate(5deg);
  }

  .mml__icon--left {
    bottom: -20px;
    left: -5%;
    transform: rotate(5deg);
  }

  .mml .projects__img {
    padding: 0 3rem;
  }
}

@media (width <= 460px) {
  .mml__icon {
    border-radius: 1.5rem;
    padding: 1rem 1.2rem;
  }

  .mml__icon svg {
    width: 5rem;
  }

  .mml__icon--top, .mml__icon--right {
    top: 5px;
  }
}

@media (width <= 375px) {
  .mml__icon svg {
    width: 4rem;
  }
}

@media (width <= 330px) {
  .mml__icon svg {
    width: 3rem;
  }
}

